.caroucard{
    width: 60%;
    margin-left: 20%;
    color: black;
    margin-bottom: 60px;
}

.img-thumbnail{
    border-radius: 50%;
    border-color: #410665;
    background-color: #410665;
}

.carousel-indicators li{
    background-color: #410665 !important;
    opacity: 1 !important;
    border-radius: 50%;
    height: 10px;
    width: 10px;
}

.carousel-indicators .active{
background-color: white !important;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
  background-image: none;
}

.carousel-control-next-icon:after
{
  content: '>';
  font-size: 30px;
  color: #410665;
  font-weight: bolder;
}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 30px;
  color:#410665;
  font-weight: bolder;
}


